// Buttons
//
//


.btn {
  display: inline-block;
  box-sizing: border-box;
  height: $btn-height;
  padding: 0 $btn-padding;
  background: $btn-bg-color;
  line-height: calc(#{$btn-height} - 1px);
  font-size: $btn-font-size;
  font-family: inherit;
  font-weight: inherit;
  letter-spacing: normal;
  color: $btn-text-color;
  cursor: pointer;
  outline: none;
}

.btn-sm {
  height: $btn-height-sm;
  padding: 0 $btn-padding-sm;
  line-height: calc(#{$btn-height-sm} - 1px);
  font-size: $btn-font-size-sm;
}
.btn-lg {
  height: $btn-height-lg;
  padding: 0 $btn-padding-lg;
  line-height: calc(#{$btn-height-lg} - 1px);
  font-size: $btn-font-size-lg;
}
.btn-white {
  color: #fff;
  border-color: #fff;
}
.btn-circle {
  border-radius: 50%;
  width: rem(30px);
  height: rem(30px);
  padding: 0;
  display: block;
  margin: 0 auto;
  color: #fff;
  line-height: rem(30px);
  text-align: center;
}
.btn-blue, .btn-aqua {
  color: #fff;
  border: none;
}
.btn-blue {
  background-color: $blue;
}
.btn-aqua {
  background-color: $aqua;
}
.btn-more {
  background-color: rgb(239, 239, 239);
  width: 100%;
  height: 50px;
  text-align: center;
  color: #000;
  border: none;
}